import * as db from "~/core/database";

export const update = async ({ id, name, color, description }) =>
  db.update(
    `
      update subjects SET
      name = coalesce(?, name)
      , color = coalesce(?, color)
      , description = coalesce(?, description)
      where id = ?
    `,
    [name, color, description, id]
  );
